@font-face {
    font-family: 'FontAwesome';
    src: local('FontAwesome'), url('//cdn.bootcss.com/font-awesome/4.5.0/fonts/fontawesome-webfont.ttf') format('truetype');
}

body {
    font-family: 'FontAwesome', 'Helvetica Neue', Helvetica, Tahoma, Arial, STXihei, 'Microsoft YaHei', '微软雅黑', sans-serif;
    background-color: transparent;
    background-image: url(../img/bg.jpg);
    background-repeat: repeat;
    padding-top: 100px;
    padding-bottom: 50px;
}

.box {
    color: #D2322D !important;
    float: left;
    text-decoration: none !important;
    width: 175px;
    min-height: 100px;
    margin: 8px;
    padding: 15px;
    -webkit-transition: top 1s ease, left 1s ease;
    -moz-transition: top 1s ease, left 1s ease;
    -ms-transition: top 1s ease, left 1s ease;
    -o-transition: top 1s ease, left 1s ease;
    background: #fff;
    -webkit-box-shadow: 0 1px 5px rgba(34, 25, 25, .4);
    -moz-box-shadow: 0 1px 5px rgba(34, 25, 25, .4);
    box-shadow: 0 1px 5px rgba(34, 25, 25, .4);
    padding-bottom: 10px!important;
    text-align: center;
    background-color: white!important;
    border-radius: 4px;
}

.box p {
    font-size: 17px;
    display: block;
    margin: 10px 0;
    padding: 0 0 5px;
    border-bottom: 1px solid #ccc;
}

.box img {
    max-width: 100%;
    background: #ccc;
    overflow: hidden;
    width: expression(document.body.clientWidth>190?"190px": "auto");
}
